import { Carousel } from 'react-responsive-carousel';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import firebase from './firebase.js';
import { setLogLevel } from 'firebase';


export default class Message extends Component
{
    
    state={
        loop:[],
    };
    
    message=()=>
    {
        var str=window.location.href.split("/").splice(-1)
        var temp=firebase.database().ref(str+"/Message");
        var arr=[]
        var demo
        temp.on("value", function(snapshot) {
            demo=snapshot.val();
            var count=0;
            for(var i in demo)
            {
                if(count==0)
                {
                    arr.push(
                        <div class="carousel-item active">
                            <h1>{demo[i]}</h1>
                        </div>
                        );
                        count=1;
                }
                else{
                    arr.push(
                        <div class="carousel-item">
                            <h1>{demo[i]}</h1>
                        </div>
                        );
                    

                }
                
            }    
            
          }, function (errorObject) {
            console.log("The read failed: " + errorObject.code);
          });

          this.setState({loop:arr})
        
        console.log(demo)
        console.log("$$$$$$$$$$$$$$$$$$$$$$$$$$$$$$$$")
        
    }
    render()
    {
        
        return (
            <div>
            <button className="btn btn-primary" onClick={this.message}>Click here to see msg</button>
            <div className="container">
            <div id="carouselExampleSlidesOnly" class="carousel slide" data-ride="carousel">
            {this.state.loop}
          </div> 
          </div>
          </div>    

        );
    }
}