import  React,{ Component } from "react";
import firebase from './firebase.js';
import {Link} from "react-router-dom";


export default class Send extends Component
{
  state={
    message:"",
  }
  Send=()=>
  {
      var str=window.location.href.split("/").splice(-1)
      var temp=firebase.database().ref(str+"/Message");
       temp.push(this.state.message)

  }
  Change=(e)=>
  {
    this.setState({
        message: e.target.value
      }); 
  }
  render()
  {
      return(
        <div className='container'>
          <h1>Vignesh is here</h1>
          <section className='add-item'>
                  <div className="form-group">
                    <label>Enter the message here</label>
                    <input className="form-control form-control-lg" type="text" name="username"  onChange={this.Change} value={this.state.message} />
                  </div>
        </section>
        <Link  to ="/Login" onClick={this.Send}  className="btn btn-primary">Click here</Link>
                  
        </div>
      );
  }
}