
import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import firebase from './firebase.js';
import {Link} from "react-router-dom";
import Linkes from './linkes.js'
import Image from 'react-bootstrap/Image'


class Login extends Component {
  constructor() {
    super();
    this.state = {
      currentItem: '',
      username: '',
      change:false,
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  handleSubmit(e) {
    console.log(window.location.href)
    e.preventDefault();
    const itemsRef = firebase.database().ref('items');
    const item = {
      title: this.state.currentItem,
      user: this.state.username
    }
    itemsRef.push(item);
    this.setState({
      currentItem: '',
      username: ''
    });
  }
  componentDidMount() {
    const itemsRef = firebase.database().ref('items');
    itemsRef.on('value', (snapshot) => {
      let items = snapshot.val();
      let newState = [];
      for (let item in items) {
        newState.push({
          id: item,
          title: items[item].title,
          user: items[item].user
        });
      }
      this.setState({
        items: newState
      });
    });
  }
  removeItem(itemId) {
    const itemRef = firebase.database().ref(`/items/${itemId}`);
    itemRef.remove();
  }
  Change=()=>
  {
    this.setState({change:true})
    const itemRef = firebase.database().ref(this.state.username+"/pass");
    itemRef.push(this.state.currentItem);
    
  }
  render() {
    var display=null;
    if(this.state.change)
    {
    display=<Linkes name={this.state.username} pass={this.state.currentItem}/>                  
    }       

    return (

      <div className='app'>
        <header>
            <div className="wrapper">
              <h1><u>Message Sent</u></h1>
              <p></p>
              <p></p>
              <p></p>
              <p></p> 
               
              <h2>To create your link enter the details below </h2>               
            </div>

        </header>
        <div className='container'>
          <section className='add-item'>
                  <div className="form-group">
                    <label>Enter Name</label>
                    <input className="form-control form-control-lg" type="text" name="username"  onChange={this.handleChange} value={this.state.username} />
                  </div>
                  <div className="form-group">
                    <label>Enter a password</label>
                    <input className="form-control form-control-lg" type="text" name="currentItem"  onChange={this.handleChange} value={this.state.currentItem} />
                  </div>
                  <Link onClick={this.Change}  className="btn btn-primary">Click here</Link>
                    
                  {display}
          </section>
          </div> 
      </div>
    );
  }
}
export default Login;